import React from 'react';
import '../css/cashout.scss';

/*
  class Cashout is a react component to handle the cash out details
*/
class CashOut extends React.Component{
    /*
     constructor to inialize the state variables of the cashout page
    */
    constructor(props){
        super(props);
        this.state={
            purpose : '',
            date : '',
            amount : ''
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    /*
     This method is used to set the input values entered by user to the state
     This method is invoked every time when user enter any values in the input fields
    */
    handleChange = tags => (event) => {
        this.setState({
          [tags]:event.target.value
        }) 
    }

    /*
     This method is used to register the cashout details entered
     This method is invoked every time when user clicks submit button
    */
    handleSubmit(e){
        e.preventDefault();
        console.log(this.state);
    }

    /*
     This method return a jsx in which our actual elements are displayed in the screen
     This method invoked by default when we use this component(CashOut class)
     Invoked whenever the state of this component is changed
    */
    render(){
        return(
        <div className="maindiv">
        <form onSubmit={this.handleSubmit} className="mainform" >
          <label>Purpose</label>
          <input type="text" placeholder="Enter purpose" name="purpose"
            onChange={this.handleChange('name')} required className="inputitemscashout"></input>
          <br></br>
          <label>Date</label>
          <input type="date" name="date"
            onChange={this.handleChange('date')} required className="inputitemscashout"></input>
          <br></br>
          <label>Amount</label>
          <input type="number" name="amount" placeholder="Enter amount"
            onChange={this.handleChange('amount')} required className="inputitemscashout"></input>
          <br></br>
          <button type="submit" className="submitbtncashout" value="Login">Submit</button>
        </form>
      </div>
        )
    }
}

/*
  Exporting the cashout component such that any component can use this component by importing this component
*/
export default CashOut;