import React from 'react';
import '../css/login.scss';
import Modal from 'react-awesome-modal';
/*
  class Login is a react component to handle the login page functionalities
*/
class Login extends React.Component {
  /*
  constructor to inialize the state variables of the login page
  */
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password:'',
      visible : false,
      email : ''
    };
  this.handleChange         = this.handleChange.bind(this);
  this.handleClear          = this.handleClear.bind(this);
  this.handleSignUpClick    = this.handleSignUpClick.bind(this);
  this.handleForgotPassword = this.handleForgotPassword.bind(this);
  this.openModal            = this.openModal.bind(this);
  this.closeModal           = this.closeModal.bind(this);
  this.handleSubmit         = this.handleSubmit.bind(this);
  }

  /*
    This method is used to set the input values entered by user to the state
    This method is invoked every time when user enter any values in the input fields
  */
  handleChange = tags => (event) => {
    this.setState({
      [tags]:event.target.value
    })
  }

  /*
    This method is used to clear the values of username and password entered by the user
    This method is invoked when user click the clear button
  */
  handleClear(event) {
    this.setState({
      username : '',
      password : ''
    })
  }

    /*
    This method is used to redirect to signup page
    This method is invoked when user click the signup button
  */
 handleSignUpClick() {
   this.props.history.push('/signup');
}

      /*
    This method is used to change the password
    This method is invoked when user click the forgot password button
  */
 handleForgotPassword(event) {
   event.preventDefault();
   this.props.history.push(event.target.pathname);
 }

  /*
    This method is used to validate the login credentials entered by user
    This method is invoked when user click the login button
  */
  handleSubmit(event) {
    event.preventDefault();
    console.log(this.state);
    this.props.history.push('/homepage');
    /*fetch('http://localhost:8080/api/login', {
      method: 'POST',
      headers: {
        'Access-Control':'Allow-Origin',
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify(this.state),
    })
    .then(response => response.json())
    .then(data => {
    console.log('Success:', data);
    });*/
  }
  
  openModal(e) {
    e.preventDefault();
    this.setState({
        visible : true
    });
  }

  closeModal() {
    this.setState({
        visible : false,
        email : ''
    });
  }

  /*
    This method return a jsx in which our actual elements are displayed in the screen
    This method invoked by default when we use this component(Login class)
    Invoked whenever the state of this component is changed
  */
  render(){
  return (
      <div className="maindiv">
        <button  className="signupbutton" onClick={this.handleSignUpClick}>Signup</button>
        <form onSubmit={this.handleSubmit}>
          <div className="imgcontainer">
            <img src="./src/images/img_avatar2.png" alt="Avatar" className="avatar"></img>
          </div>  
          <p className="welcome">Welcome to cash flow</p>
          <label >Username </label>
          <input type="text" placeholder="Enter username" name="username" value={this.state.username} 
          onChange={this.handleChange('username')} className="inputitemslogin" required></input>
          <br></br>
          <label>Password </label>
          <input type="password" placeholder="Enter password" name="password" required value={this.state.password} 
          onChange={this.handleChange('password')} className="inputitemslogin" ></input>
          <br></br>
          <button className="loginbutton" type="submit" value="Login">Login</button>
          <button type="button" className="clearbtnlogin" onClick={this.handleClear}>Clear</button>
          <span className="password">Forgot <button onClick={this.openModal}>password?</button></span>
        </form>
        <Modal visible={this.state.visible} width="400" height="300" effect="fadeInUp" 
        onClickAway={this.closeModal} className="modal">
                    <div>
                    <h5 className="modalheading">Enter registered emailid to change password</h5>
            <input type="email" className="modalinput" placeholder="Enter registered email" 
            onChange={this.handleChange('email')} value={this.state.email}></input>
            <br></br>
            <button className="modalsubmitbtn">Submit</button>
                    </div>
          </Modal>
      </div>
  );
}
}

/*
  Exporting the login component such that any component can use this component by importing this component
*/
export default Login;