import React from 'react';
import '../css/signup.scss';

/*
  class Signup is a react component to handle the Signup page functionalities
*/
class Signup extends React.Component {
  /*
  constructor to inialize the state variables of the signup page
  */
  constructor(props) {
    super(props);
    this.state = {
      username : '',
      repeatpassword:'',
      password : '',
      emailID : '',
      country : '',
      phone : ''
    };
  this.handleChange = this.handleChange.bind(this);
  this.handleCancel = this.handleCancel.bind(this);
  this.handleSubmit = this.handleSubmit.bind(this);
  }

  /*
    This method is used to set the input values entered by user to the state
    This method is invoked every time when user enter any values in the input fields
  */
  handleChange = tags => (event) => {
   this.setState({
     [tags]:event.target.value
   }) 
  }

  /*
    This method is used to redirect back to login page
    This method is invoked when cancel button is clicked
  */
 handleCancel(event) {
    this.props.history.goBack();
  }

  /*
    This method is used to create a new account for the user
    This method is invoked when user click the createaccount button
  */
  handleSubmit(event) {
    event.preventDefault();
    console.log(this.state);
    fetch('http://localhost:8080/api/signup', {
      method: 'POST',
      headers: {
        'Access-Control':'Allow-Origin',
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify(this.state),
    })
    .then(response => response.json())
    .then(data => {
    console.log('Success:', data);
});
  }
  
  /*
    This method return a jsx in which our actual elements are displayed in the screen
    This method invoked by default when we use this component(Signup class)
    Invoked whenever the state of this component is changed
  */
  render(){
  return (
      <div className="maindiv">
        <form onSubmit={this.handleSubmit} className="mainform" >
          <h1>Sign Up</h1>
          <p>Please fill in this form to create an account.</p>
          <hr></hr>
          <label >Username </label>
          <input type="text" placeholder="Enter username" name="username"
            onChange={this.handleChange('username')} required className="inputitemssignup"></input>
          <br></br>
          <label>Password </label>
          <input type="password" placeholder="Enter password" name="password" 
            onChange={this.handleChange('password')} required className="inputitemssignup"></input>
          <br></br>
          <label>Repeat Password </label>
          <input type="password" placeholder="Repeat password" name="repeatpassword" 
            onChange={this.handleChange('repeatpassword')} required className="inputitemssignup"></input>
          <br></br>
          <label >EmailID </label>
          <input type="email" placeholder="Enter emailID" name="emailId"
            onChange={this.handleChange('emailID')} required className="inputitemssignup"></input>
          <br></br>
          <label >Country </label>
          <input type="text" placeholder="Enter country" name="country"
            onChange={this.handleChange('country')} className="inputitemssignup"></input>
          <br></br>
          <label>Phone</label>
          <input type="tel" placeholder="Enter phone number" name="phone"
            onChange={this.handleChange('phone')} required className="inputitemssignup"></input>
          <br></br>
          <div className="buttonsdiv">
            <button type="button" className="cancelbtnsignup" onClick={this.handleCancel}>Cancel</button>
            <button type="submit" className="createaccountbtn" value="Login">CreateAccount</button>
          </div>
        </form>
      </div>
  );
}
}

/*
  Exporting the signup component such that any component can use this component by importing this component
*/
export default Signup;